import React from "react";
import {
  InfoContainer,
  InfoWrapper,
  InfoRow,
  Column1,
  Column2,
  TextWrapper,
  TopLine,
  Heading,
  SubTitle,
  ImgWrap,
  Img,
  Img2,
  TopLine1,
} from "./InfoElements";

const InfoSection = ({
  lightBg,
  id,
  imgStart,
  topLine,
  lightText,
  headLine,
  darkText,
  description,
  img,
  img1,
  description2,
  description3,
  description4,
  description5,
  description6,
  description7,
  description8,
  description9,
  topLine1,
}) => {
  return (
    <>
      <InfoContainer lightBg={lightBg} id={id}>
        <InfoWrapper>
          <InfoRow imgStart={imgStart}>
            <Column1>
              <TextWrapper>
                <TopLine>{topLine}</TopLine>

                <Heading lightText={lightText}> {headLine} </Heading>
                <SubTitle darkText={darkText}>{description}</SubTitle>
                <SubTitle darkText={darkText}>{description2}</SubTitle>
                <SubTitle darkText={darkText}>{description3}</SubTitle>
                <SubTitle darkText={darkText}>{description4}</SubTitle>
                <SubTitle darkText={darkText}>{description5}</SubTitle>
                <TopLine1>{topLine1}</TopLine1>
                <SubTitle darkText={darkText}>{description6}</SubTitle>
                <SubTitle darkText={darkText}>{description7}</SubTitle>
                <SubTitle darkText={darkText}>{description8}</SubTitle>
                <SubTitle darkText={darkText}>{description9}</SubTitle>
                {/* <BtnWrap>
                    <Button to="home" 
                    smooth={true}
                    duration={500}
                    spy={true}
                    exact="true"
                    offset={-80}
                    primary={ primary ? 1 : 0}
                    dark={dark ? 1 : 0}
                    dark2={dark2 ? 1 :0 }

                    > {buttonLabel} </Button>
                    
                </BtnWrap> */}
              </TextWrapper>
            </Column1>
            <Column2>
              <ImgWrap>
                <Img src={img} />
                <Img2 src={img1} />
              </ImgWrap>
            </Column2>
          </InfoRow>
        </InfoWrapper>
      </InfoContainer>
    </>
  );
};

export default InfoSection;
