import React, { useState } from "react";
import Video from "../../videos/video.mp4";
import { Button } from "../ButtonElements";

import {
  FaBehance,
  FaGithub,
  FaLinkedin,
  FaTwitter,
  FaFacebook,
  FaInstagram,
} from "react-icons/fa";
import {
  HeroContainer,
  HeroBg,
  VideoBg,
  HeroContent,
  HeroH1,
  HeroP,
  HeroBtnWrapper,
  ArrowForward,
  ArrowRight,
  HeroSocialIcons,
  HeroSocialIconLink,
  HeroSocialMediaWrap,
} from "./HeroElements";

const HeroSection = () => {
  const [hover, setHover, scrollNav] = useState(false);

  const onHover = () => {
    setHover(!hover);
  };

  return (
    <HeroContainer id="home">
      <HeroBg>
        <VideoBg autoPlay loop muted src={Video} type="video/mp4" />
      </HeroBg>
      <HeroContent>
        <HeroH1> Hello ! I'm Ma. Nikka Herrera üë©‚Äçüíª</HeroH1>
        <HeroP> I am a üíªtech and üé® design enthusiast</HeroP>
        <HeroSocialMediaWrap>
          <HeroSocialIcons>
            <HeroSocialIconLink
              href="//www.facebook.com/nikxherrera/"
              target="_blank"
              aria-label="Facebook"
            >
              <FaFacebook />
            </HeroSocialIconLink>

            <HeroSocialIconLink
              href="//www.instagram.com/nikxherrera/"
              target="_blank"
              aria-label="Instagram"
            >
              <FaInstagram />
            </HeroSocialIconLink>

            <HeroSocialIconLink
              href="//www.linkedin.com/in/nikxherrera/"
              target="_blank"
              aria-label="Linkedin"
            >
              <FaLinkedin />
            </HeroSocialIconLink>

            <HeroSocialIconLink
              href="//github.com/nikxherrera"
              target="_blank"
              aria-label="Github"
            >
              <FaGithub />
            </HeroSocialIconLink>

            <HeroSocialIconLink
              href="//www.behance.net/nikxherrera"
              target="_blank"
              aria-label="Behance"
            >
              <FaBehance />
            </HeroSocialIconLink>

            <HeroSocialIconLink
              href="//twitter.com/nikxherrera"
              target="_blank"
              aria-label="Twitter"
            >
              <FaTwitter />
            </HeroSocialIconLink>
          </HeroSocialIcons>
        </HeroSocialMediaWrap>
        <HeroBtnWrapper scrollNav={scrollNav}>
          <Button
            to="about"
            onMouseEnter={onHover}
            onMouseLeave={onHover}
            primary="true"
            dark="true"
            smooth={true}
            duration={500}
            spy={true}
            exact="true"
            offset={-80}
          >
            Get to know more {hover ? <ArrowForward /> : <ArrowRight />}
          </Button>
        </HeroBtnWrapper>
      </HeroContent>
    </HeroContainer>
  );
};

export default HeroSection;
